 .postcard {
  margin-left: 50px;
  border-radius: 15px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  margin: 5px 0px 50px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
}

/* Style for the images (you can customize this as needed) */
.postcard img {
  width: 300px; /* Adjust the image width as needed */
  height: 250px; /* Maintain the aspect ratio */
}

/* Style for the text below the images */
.postcard p {
  background-color: white;
  border-radius: 20px;
  padding: 7px;
  margin-top: 10px; /* Add space between image and text */
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  color: rgba(152, 4, 85, 0.951);
}

 